body {
  font-family: 'Montserrat', sans-serif;
  background: rgba(59, 76, 146, 0.5);
  width: 100%;
  height: 100%;
  margin: 0;
}

.container {
  max-width: 1700px;
  width: 95%;
  margin: 0 auto;
}

#header .center {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  color: #fff;
}

#header .formWrapper {
  background: #fff;
  padding: 30px 40px;
  border-radius: 7px;
  color: #3e485c;
}

#header .formWrapper .form-group {
  margin-bottom: 15px;
}

#header .formWrapper label {
  font-weight: 700;
  font-size: 0.8em;
}

#header .formWrapper input,
#header .formWrapper select {
  background: #ebecee;
  border: none;
  font-size: 14px;
  height: 40px;
  padding: 5px;
}

#header .formWrapper input:focus {
  box-shadow: none;
}

#header .formWrapper button {
  background: #5c5cf1;
  color: #fff;
  font-weight: 700;
  padding: 12px 30px;
  font-size: .9em;
}

.row__item_result {
  background: #fff;
  padding: 30px 15px;
  border-radius: 7px;
  color: #3e485c;
  margin-top: 20px;
  min-height: 550px;
  font-size: 14px;
}

.param__name {
  margin-bottom: 17px;
  display: flex;
  font-size: 16px;
}
.param__name div {
  width: 130px;
}
.param__name div:nth-child(5) {
  width: 220px;
}
.param__name div:last-child {
  width: 285px;
}
.param__row-result {
  text-align: center;
}

.param__name div:nth-child(1) {
  width: 200px;
}

.error {
  color: red;
}
.result {
  font-weight: bold;
  display: flex;
  margin-bottom: 10px;
}
.old_result {
  display: flex;
}
.old_result div {
  margin-right: 15px;
  margin-bottom: 10px;
  width: 130px;
}
.result div {
  margin-right: 15px;
  width: 130px;
}
.result div:nth-child(1) {
  margin-right: 15px;
  width: 200px;
}
.old_result div:nth-child(1) {
  margin-right: 15px;
  width: 200px;
}

.old_result div:last-child {
  width: 285px;
  word-wrap: break-word;
}
.result div:last-child {
  width: 285px;
  word-wrap: break-word;
}

.old_result div:nth-child(5) {
  width: 270px;
  text-align: initial;
}
.result div:nth-child(5) {
  width: 270px;
  text-align: initial;
}
